import React, { Component } from 'react';
import {
  Link
} from 'react-router-dom';

class Header extends Component {
  render() {
    return (
      <nav className="flex justify-between bb  bg-black b--white-10">
        <a className="link white-70 hover-white no-underline flex items-center pa3">
        </a>
        <div className="flex-grow pa3 flex items-center">
          <Link className="f6 link dib white dim mr3 mr4-ns" to="/">Home</Link>
          <Link className="f6 link dib white dim mr3 mr4-ns" to="/about">About</Link>
          <Link className="f6 link dib white dim mr3 mr4-ns" to="/blog">Blog</Link>
        </div>
      </nav>
    );
  }
}

export default Header;



